<template>
    <div style="postion: relative;">
        <p-button
            v-if="hasNewMessage"
            :label="messageCount.toString()"
            icon="pi pi-bell"
            class="p-button-warning"
        />
    </div>
</template>

<script>
import { inject, onMounted, onUnmounted, ref } from "vue";
import { WEB_SOCKET_URI } from "@/constants";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";
export default {
    setup() {
        const store = useStore();
        const hasNewMessage = ref(false);
        const messageCount = ref(0);

        const setupSocketConnection = () => {
            const websocket = inject("websocket");

            websocket.onmessage = (event) => {
                const data = JSON.parse(event.data);

                if (data.type == "usermsg" && data.terminal == "nlp") {
                    hasNewMessage.value = true;
                    messageCount.value = messageCount.value + 1;
                }
            };
        };

        const messageSeenHander = () => {
            hasNewMessage.value = false;
            messageCount.value = 0;
        };

        onMounted(() => {
            // setupSocketConnection();

            emitter.on("message-nofication:has-seen", messageSeenHander);
        });

        onUnmounted(() => {
            emitter.off("message-nofication:has-seen", messageSeenHander);
        });

        return {
            hasNewMessage,
            messageCount
        };
    }
};
</script>

